/* // Extra small devices (portrait phones, less than 576px) */
@media (max-width: 575.98px) {
  .home-my-info {
    width: 90% !important;
  }
  .about-info-box {
    width: 300px !important;
  }
}

/* // Small devices (landscape phones, 576px and up) */
@media (min-width: 576px) and (max-width: 767.98px) {

}

/* // Medium devices (tablets, 768px and up) */
@media (min-width: 768px) and (max-width: 991.98px) {
  .home-heading {
    font-size: 34px !important;
  }
}

/* // Large devices (desktops, 992px and up) */
@media (min-width: 992px) and (max-width: 1199.98px) {
  .nav-link {
    padding: 0 20px !important;
  }
}

/* // Extra large devices (large desktops, 1200px and up) */
@media (min-width: 1200px) {
  .nav-link {
    padding: 0 20px !important;
  }
}

@media(min-width: 768px) {

}

@media(max-width: 767px) {
  .home-heading {
    font-size: 30px !important;
  }

  
}

@media(max-width: 991px) {
  .navbar-collapse {
    background-color: white;
  }
  .overlay_color {
    width: 100vw !important;
  }
  .nav-link {
    padding: 16px 0 !important;
  }

  .navbar-toggler {
    display: flex;
    justify-content: center;
    align-items: center;
  }
}

* {
  user-select: none;
}

html {
  max-width: 100vw;
  overflow-x: hidden;
}

/* ScrollBar */
/* width */
::-webkit-scrollbar {
  width: 8px;
}

/* Track */
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px #80808000; 
  border-radius: 10px;
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  background: #9885ff; 
  border-radius: 10px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #7a62ff; 
}

.App {
  text-align: center;
  background-color: #f3f7fd;
}

section {
  min-height: 100vh;
  padding-top: 120px;
  display: flex;
  justify-content: center;
  align-items: center;
}




/* Navbar */

.add-shadow {
  box-shadow: 0 2px 35px 0 rgb(0 0 0 / 10%);
  background-color: #fff !important;
}

.navbar.bg-light {
  background-color: #f8f9fa00;
}

.navbar {
  font-family: 'Montserrat', sans-serif;
  font-weight: 600;
}

.navbar-toggler-icon {
  background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' width='30' height='30' viewBox='0 0 30 30'%3e%3cpath stroke='rgba%28255, 255, 255, 1%29' stroke-linecap='round' stroke-miterlimit='10' stroke-width='2' d='M4 7h22M4 15h22M4 23h22'/%3e%3c/svg%3e") !important;
}

.navbar-collapse {
  justify-content: center;
}

.nav-link {
  color: rgb(0 0 0) !important;
}

.nav-link.active {
  color: blue !important;
}

.navbar-toggler {
  border: 0 !important;
  background-color: #7a62ff !important;
  border-radius: 50% !important;
  width: 45px;
  height: 45px;
}




/* Designs */

.logo-design-img {
  top: 10px;
  left: 20px;
}
.dot-design-img {
  top: 180px;
  left: 160px;
}
.overlay_color {
  position: absolute;
  height: 100%;
  width: 35vw;
  right: 0;
  top: 0;
  background-color: #e4effd;
}
.person-design-img {
  bottom: 0;
  right: 50%;
  transform: translateX(50%);
  z-index: 2;
}
.person-design-img>img {
  height: 70vh;
}
.mern-design-img {
  top: 180px;
  right: 160px;
}
.html-design-img {
  top: 300px;
  left: 90px;
}
.react-design-img {
  bottom: 60px;
  left: 100px;
}




/* Fonts */

.font-poppins {
  font-family: 'Poppins', sans-serif;
}




/* Home */

/* #home {
  min-height: 100vh;
} */
#home {
  background-color: #f3f7fd;
  padding-top: 0 !important;
}
.take-full-screen {
  min-height: 100vh;
  margin: 0 !important;
  width: 100vw;
}
.take-full-screen>.col-12 {
  min-height: 100vh;
}
#home .container {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.home-heading {
  text-align: left;
  font-size: 4vw;
  font-weight: bold;
  margin: 0 auto 35px;
}
.home-post {
  background-color: rgb(168, 188, 253);
  padding: 18px 30px;
  border-radius: 10px;
  z-index: 3;
  font-family: 'Poppins', sans-serif;
  margin-bottom: 35px;
  letter-spacing: 5px;
}
.home-my-info {
  width: 70%;
  line-height: 2;
}




/* About */
.about-info-box {
  width: 450px;
  padding: 25px 15px;
  background-color: rgb(255, 255, 255);
  border-radius: 12px;
  box-shadow: 5px 6px 20px 0px #989898;
}
.about-category-box{
  padding: 25px 35px;
  margin: 25px 0;
  border-radius: 12px;
  background-color: white;
  cursor: pointer;
}
.about-category-box-heading {
  font-weight: bold;
  font-size: 24px;
  margin-bottom: 4px;
  color: rgb(68, 68, 68)
}
.about-category-box-info {
  margin-bottom: 14px;
}
.about-category-box-number {
  font-weight: bold;
  color: rgb(255, 112, 112);
}
.about-intro-box {
  padding: 25px;
}
.about-intro-box-info {
  padding: 35px 25px;
  font-size: 18px;
  line-height: 1.7;
}
.about-cat-box-link:hover {
  text-decoration: none;
}
.about-cat-box-link {
  color: #000;
}


/* Portfolio */

.project-box {
  min-height: 280px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 15px;
  cursor: pointer;
}

.card {
  padding: 15px;
  font-family: 'Poppins';
}

.card > img {
  border-radius: 8px;
  aspect-ratio: 16/9;
}

.card button {
  padding: 8px 28px;
}

#portfolio .container > div {
  padding-bottom: 45px;
}




/* Contact */
.contact-box-container {
  text-align: left;
  padding: 35px 15px;
  /* background-color: #177cff; */
}

.contact-box-container > div {
  display: flex;
  align-items: center;
  font-family: 'Poppins';
  cursor: pointer;
}

.contact-info {
  padding-left: 15px;
  font-size: 25px;
}

.contact-box {
  height: 100px;
  display: flex;
  align-items: center;
  text-align: left;
}

.contact-form-container {
  padding: 35px 15px;
}

.form-row > div {
  text-align: left;
  padding: 18px 16px !important;
}

form > button {
  margin: 40px 0;
}

input {
  padding: 22px 25px !important;
}
